export default function WeekBlock(props) {
  return (
    <div
      className={
        "w-2 h-2 rounded-sm transform hover:scale-125 " +
        (props.past
          ? "bg-yellow-300"
          : props.current
          ? "bg-green-500"
          : props.future
          ? "bg-blue-300"
          : props.empty
          ? "bg-transparent"
          : "")
      }
    ></div>
  );
}
